import React, {Component} from 'react';
import TextField from '../TextField';
import FlatButton from 'material-ui/FlatButton';
import axios from 'axios';

class SearchComponent extends Component {
    constructor(props){
        super(props);
        this.state = {
                searchText: "",
                planets: [],
                relatedPlanet: [],
                selectedPlanet: ""
        }
    }

    changePlanet = (event) => {
        const value = event.target.value;
        this.setState({ searchText: value });
        if (this.state.planets.results) {
          let filterPlanets = this.state.planets.results.filter(planet => {
            return planet.name.toLowerCase().includes(value.toLowerCase()) === true;
          });
          this.setState({ relatedPlanet: filterPlanets });
        }
    }
    componentDidMount() {
        axios.get('https://swapi.co/api/planets/').then(response => response.json())
        .then(planets => this.setState({ planets }))
        .catch(function (error) {
            console.log(error);
        });
    }
    render() {
        return(
            <div>
                <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <TextField
                        // ref={(element) => { this.fields.push(element); }}
                        name="searchedPlanet"
                        fullWidth
                        value={this.state.data.searchedPlanet}
                        hintText="Jupiter"
                        onChange={this.changePlanet}
                        floatingLabelText="Enter planet name"
                    />
                </div>
                <ul>
                    {this.state.relatedPlanet.map(planet => (
                        <div><p className="list-options" key={planet.name}>{this.props.props}</p></div>
                    ))}
                </ul>
                <div className="btn-login-container">
                    <FlatButton className="save-button" labelStyle={{color: "#fff", textTransform: "capitalize"}} label="Login" style={{borderRadius: "20px"}} primary={true} onClick={this.canProceed} />
                </div>
                <div className="text-center selectedPlanet">
                    {this.state.selectedPlanet !== "" && (
                    <p>You submitted the planet {this.state.selectedPlanet}</p>
                    )}
                </div>
            </div>
        )
    }
}

export default SearchComponent;